@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


#createComp {
    display: flex;
    margin: 0 0 20px 0;
}

#createComp .item {
    flex: 1 1;
}
/* text input */
#createComp .item input[type=text] {
    width:300px;
    height:50px;
    border:1px solid #445555;
    padding: 0 10px 0 10px;
}
#createComp .item input[type=text]:focus {
    outline:0;
    border: 1px dashed #5b7171}

/* radio input */

.item input[type="radio"] {
    opacity: 0;
    width: 0;
    height: 0;
}

.item label {
    display: inline-block;
    background-color: #445555;
    padding: 15px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
}

.item label:hover {
    background-color: #5b7171;
}


.item input[type="radio"]:checked + label {
    background-color:#799797;
}


/* button input */

#createComp .item button {
    width:300px;
    height:50px;
    background-color: #445555;
    border: 0 none;
    border-radius: 6px;
    color: #FFFFFF;
    cursor: pointer;
    display: inline-block;
    vertical-align: middle;
    outline: 0 none;
    text-decoration: none;
    line-height: inherit;

}

#createComp .item button:hover {
    background: #5b7171;
}

@media (max-width: 500px) {
    #createComp {    
        display: block;
    }

    #createComp .item{
        margin-bottom: 20px;
    }
}
#terminal {
    background-color:#212121;
    height: 300px;
    border-radius: 5px;
    text-align: left;
    padding: 15px;
}

#terminal div{
    color:#FFF;
}

.copyBtn {
width: 24px;
height:24px;
float:right;
display: block;
background-color:rgba(0,0,0,0);
color:#FFF;
border:none;
/* background-image: url(../../Assets/file_copy.svg) */
}

.copyBtn:focus{
    outline:0;
}

.copyBtn:hover{
    color: #212121;
    stroke:#FFF;
}

.fileCopied {
    color:#c62828;
    opacity: 0;
    font-weight: bold;
}
* {  font-family: 'Roboto Mono', monospace;}

.container{
  text-align: center;
}

.container .title{
  color:#424242;
}
